@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


.AboutMeContainer{
    background-color: #BABFBE;
    display: flex;
    margin: auto 0px;
    width: 100%;
    align-items: center;
    justify-content: space-evenly;
    padding: 30px;
}

.AboutMeContainer p{
    width:30%;
    font-family: 'Poppins', sans-serif;
    font-size: 21px;    
}

.title{
    font-size: 20px;
    text-align: center;
    padding-top: 120px;
}

.title h1{
    font-family: monospace;
    color: #736663;
}
.image{
    width: 390px;

}

@media (max-width: 500px) {
    .AboutMeContainer{
        flex-direction: column;
        align-items: center;
        padding: 0px;
    }    
    .title{
        font-size: 14px;
        padding-top: 20px;
    }
    .AboutMeContainer p{
        width: 70%;
        font-size: 18px;
    }
    .image{
        width: 290px;
    }
}