@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body{
    padding: 0px;
    margin: 0px;
}

.NavBarContainer{
    position: fixed;
    display:flex;
    align-items: center;
    justify-content: space-around;
    margin: auto;
    width: 100%;
    background-color: #0D0D0D;
    padding-bottom: 10px;

}

.aNavBar{
    display: flex;
    flex-direction: column;
    text-decoration: none;
    align-items: center;
    width: 30%;
}

.aNavBar h1{
    font-family: 'Poppins', sans-serif;
    letter-spacing: 2px;
    margin: 0px;
    color:#BABFBE;
}

.aNavBar h2{
    margin: 0px;
    font-family: monospace;
    letter-spacing: 3.8px;
    color:#BABFBE;
    font-weight: 300;

}


.UlOrder{
    display: flex;
    text-align: center;
    margin: 10px 0px 5px 0px;
    padding: 0px;
}

.UlOrder h1{
    font-family: monospace;
    font-weight: 300;
    font-size: 12px;
    margin: 0px;
    color: #32D9BA;
    transition-duration: 1s;
}

.List{
    list-style-type: none;
    margin: 0px 30px;

}

.List > img{
    width: 60px;
    animation: shinning 2s; 
    animation-iteration-count: infinite;
    animation-direction: alternate;
    transition-duration: 1s;
}

.List > img:focus, .List > img:hover, .List:hover h1{
    align-items: center;
    width: 70px;
    color: #a62e88;
    font-size: 10px;
    text-align: center;
}

.btnNavBar{
    display: none;
    background-color:#ff76dd;
    border-radius: 40px;
    box-shadow: 0px 0px 1px wheat;
}

@keyframes shinning {
    from{}
    to{
        filter: brightness(150%);
    }
}

.btnNavBar img{
    width: 40px;
}

@media screen and (max-width: 500px){
    
    .NavBarContainer{
        display: block;
        position: relative;
        flex-direction: column;
        height: auto;
        padding-bottom: 10px;
    }
    .UlOrder{
        flex-direction: column;
        height: auto;
    }
    .UlOrder h1{
        margin: auto;
        width: 50%;
    }
    .List{
        width: 40%;
        height: auto;
        right: 20px;
        margin: auto;
        border: 1px solid #a62e88;
        border-radius: 100px;
        margin-bottom: 10px;
        padding: 5px;
    }
    .btnNavBar{
       
        margin: 10px auto;
        display: block;
    }
    .aNavBar{
        padding-top: 10px;
        text-align: center;
        width: 100%;
    }
}